.display-onsiteupload{
  width: 170px;
  z-index: 10000;
  position: absolute;
  padding: 20px;
  border-radius: 20px;
  overflow: hidden;
  height: auto !important;
  margin: 10px;
}
.display-onsiteupload .overlay_bg{
  background-color: #000;
  position: absolute;
  width: 100%;
  left: 0px;
  height: 100%;
  top: 0px;
  opacity: 0.6;
}
.display-onsiteupload .positon-relative{
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  color: #fff;
  position: relative;
}
.display-onsiteupload.display-onsiteupload-text .positon-relative{
  flex-direction: row;
}
.onsiteupload_top_left_position{
  top:0px;
  left: 0px;
}
.onsiteupload_top_right_position{
  top:0px;
  right: 0px;
}
.onsiteupload_bottom_left_position{
 bottom: 0px;
 left: 0px;
}
.onsiteupload_bottom_right_position{
  bottom: 0px;
  right: 0px;
 }
.qr-with-logo.onsiteupload_bottom_right_position{
  bottom: 80px;
}

.onsiteupload_top_position{
  top:0px;
  }
  .onsiteupload_bottom_position{
   bottom: 0px;
  }

.onsite_barcode_img_{
  height: 100%;
  width: auto;
  max-width: 100%;
}

.onsite_barcode_{
  display: flex;
  flex-shrink: 1;
  align-items: center;
  height: 100%;
  min-width: 70px;
  object-fit: cover;
  border: 1px solid #fff;
  border-radius: 8px;
  background: #fff;
  padding: 5px;
  justify-content: center;
  position: relative;
}
.onsite_barcode_::after{
  content: '';
  display: block;
  position: absolute;
  bottom: -11px;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid #fff;
}
.onsite_text_{
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-top: 15px;
  font-weight: 500;
}
.display-onsiteupload-text{
  width: auto;
  flex-direction: row;
  padding: 15px;
}
.display-onsiteupload-text .onsite_barcode_{
  width: 100px;
}
.display-onsiteupload-text .onsite_barcode_::after{
  content: none;
}
.display-onsiteupload-text .onsite_text_{
  max-width: 300px;
  padding-right: 15px;
  margin-top: 0px;
}
@media screen and (max-width: 767px){
  .display-onsiteupload{
    width: 100%;
    margin: 0px;
    border-radius: 0px;
    flex-direction: row;
    padding: 10px;
  }
  .onsite_barcode_{
    margin-right: 10px;
    width: 50px;
    min-width: auto;
    border-radius: 4px;
    padding: 3px;
  }
  .onsite_barcode_::after{
    content: none;
  }
  .onsite_text_{
    margin-top: 0px;
  }
  .display-onsiteupload-text{
    height: 90px !important;
    flex-direction: row;
    width: 100%;
    margin: 0px;
    border-radius: 0px;
    padding: 15px;
  }  
  .display-onsiteupload-text .onsite_barcode_{
    width: 60px;
    height: 60px;
    min-width: auto;
    border: 0px;
    border-radius: 0px;
  }
  .classicSlideshow > div{
    opacity: 1 !important;
    transform: translate3d(0,0px,0);
  }
  .display-onsiteupload-text .onsite_text_{
    font-size: 14px !important;
    padding: 0px 10px;
    text-align: left;
    margin-top: 0px;
  }
}
